.main-board-div {
    background-color: rgb(174, 148, 96);
    border: 25px outset rgb(33, 33, 33);
    height: 40vw;
    width: 65vw;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: visible;
}

.sticky-note-div {
    position: absolute;
    height: 10vw;
    width: 10vw;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 4px 8px 10px rgba(24, 24, 24, 0.6);
    box-sizing: border-box;
}

.sticky-note-textarea {
    height: calc(100% - 20px);
    width: calc(100% - 20px);
    text-align: left;
    font-size: 17px;
    background-color: rgb(0, 0, 0, 0);
    border-width: 0px;
    resize: none;
    font-family: "Comic Sans MS";
    color: rgb(5, 5, 5);
    font-weight: 600;
}
.list-div {
    position: absolute;
    height: 22vw;
    width: 12vw;
    box-shadow: 4px 8px 10px rgba(24, 24, 24, 0.6);
    font-family: inherit;
    box-sizing: border-box;
    background-color: white;
    overflow: visible;
}


.add-list-item {
    position: relative;
    float: right;
    margin-top: -25px;
    margin-right: 10px;
    height: 40px;
    width: 35px;
    color: rgba(150, 150, 150, 0.5);
    font-size: 40px;
    background-color: rgba(0, 0, 0, 0);
    border-color: rgba(0, 0, 0, 0);
}

.list-item {
    position: relative;
    margin-top: 0px;
    margin-bottom: -15px;
    margin-left: -30px;
    width: 100%;
    height: -webkit-min-content;
    height: min-content;
    color: black;
    font-family: "Comic Sans MS";
    font-weight: 600;
    font-size: 22px;
}

.list-item > span {
    color: inherit;
    margin-left: -15px;
    line-height: 10px;
    height: -webkit-min-content;
    height: min-content;
}

.list-item-underline {
    width: 100%;
    height: 10px;
    top: -15px;
    position: relative;
    background-color: rgba(50, 50, 50, 0.2);
    display: inline-block;
    visibility: visible;
}

.list-div > ul {
    position: relative;
    margin-top: 0px;
    margin-left: 0%;
    list-style: square inside;
    width: 90%;
    max-height: 85%;
    overflow-y: scroll;
    overflow-x: visible;
}

.new-item-input {
    color: black;
    margin-top: -30px;
    font-family: "Comic Sans MS";
    font-weight: 600;
    font-size: 20px;
    margin-left: 5%;
    height: 20px;
    width: 90%;
    background-color: rgba(0, 0, 0, 0);
}

.delete-list-item-btn {
    display: inline;
    position: relative;
    float: right;
    color: rgba(225, 30, 30);
    background-color: rgba(0, 0, 0, 0);
    border-width: 0px;
    height: 20px;
    margin-bottom: -20px;
    top: -55px;
    font-weight: 900;
    font-size: 20px;
}
.sheet-div {
    position: absolute;
    height: 24vw;
    width: 18vw;
    background-color: white;
    box-shadow: 4px 8px 10px rgba(24, 24, 24, 0.6);
    font-family: inherit;
    box-sizing: border-box;
    overflow: visible;
}

.sheet-textarea {
    height: 90%;
    width: 90%;
    text-align: left;
    font-size: 18px;
    background-color: rgb(0, 0, 0, 0);
    border-width: 0px;
    resize: none;
    font-family: "Comic Sans MS";
    color: rgb(5, 5, 5);
    font-weight: 600;
    position: relative;
    left: 5%;
    top: 0%;
    line-height: 25.5px;
    max-lines: 13;
    overflow-y: scroll;
}
#board-tools-div {
    height: 70px;
    width: 200px;
    position: absolute;
    top: 30px;
    left: 100px;
    display: flex;
    align-items: center;
}

.comp-btn {
    font-weight: 900;
    font-size: 25px;
    height: 60px;
    width: 60px;
    border-radius: 15px;
    margin-right: 5px;
}

#delete-comp-btn {
    background-color: #b54545;
    border: 5px solid #872a2a;
}

#move-comp-btn{
    background-color: #e7c145;
    border: 5px solid #da9f21;
}
#welcome-div {
    width: 45%;
    height: 35%;
    overflow: visible;
}

#welcome-div > h1 {
    margin-top: 15%;
    width: 100%;
    left: 0px;
    text-align: center;
    font-size: 120px;
    font-weight: 900;
    color: white;
    -webkit-text-stroke: 3px rgb(16, 34, 22);
}
#welcome-div > h3 {
    margin-top: -5%;
    width: 100%;
    left: 0px;
    text-align: center;
    font-size: 20px;
    color: rgb(16, 34, 22);
}
* {
    color: white;
}

#nav-div {
    z-index: 2;
    position: fixed;
    left: 0px;
    top: 0px;
    width: 300px;
    height: calc(100vh - 18px);
    background-color: rgb(16, 34, 22);
    padding-left: 10px;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    border: 10px solid rgb(3, 12, 4);
}

#add-board-button {
    height: 60px;
    width: 60px;
    font-size: 40px;
    background-color: rgba(0, 0, 0, 0);
    border-width: 0px;
    float: right;
    margin-top: 20px;
    margin-right: 5px;
    font-weight: 100;
}

#board-name-input {
    background-color: rgb(16, 34, 22);
    margin-left: 10px;
    height: 20px;
    font-size: 15px;
    width: 200px;
    margin-top: 15px;
}

#my-boards-text {
    margin-top: 30px;
    font-size: 35px;
    display: inline-block;
    margin-bottom: 5px;
    margin-left: 5px;
}

.nav-item-div {
    margin-left: 20px;
    width: 280px;
    height: 55px;
}

.nav-item-div > h3 {
    font-size: 25px;
    color: white;
    width: 180px;
    height: 55px;
    overflow: hidden;
}

.nav-item-btn {
    height: 28px;
    width: 28px;
    border: 2px solid;
    border-radius: 5px;
    display: inline;
    float: right;
    position: relative;
    top: -80px;
    margin-right: 10px;

    display: none;
}

.nav-item-div:hover .nav-item-btn {
    display: inline;
}

.add-component-btn {
    background-color: #99e675;
    border-color: #559e33;
    font-size: 20px;
    font-weight: 900;
}

.delete-board-btn {
    background-color: #b54545;
    border-color: #872a2a;
    font-weight: 900;
    font-size: 12px;
}

* {
    overflow: hidden;
}

#background-div {
    background-color: rgb(164, 199, 164);
    height: calc(100vh + 50px);
    width: calc(100vw - 250px);
    position: fixed;
    left: 275px;
    top: -25px;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
    background-image: url(/cork-board-app/static/media/tree.57d60c2a.png);
    background-size: 80px;
    
}
#add-comp-menu-div {
    height: 300px;
    width: 70px;
    background-color: rgb(16, 34, 22);
    position: absolute;
    top: calc(50% - 150px);
    float: right;
    right: 20px;
    border: 10px solid rgb(3, 12, 4);
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: visible;
}

#add-comp-menu-div > ul {
    width: 100%;
    left: 0px;
    overflow: visible;
}

#add-comp-menu-div > ul > li {
    position: relative;
    margin-bottom: 10px;
    font-size: 25px;
    width: 60px;
    left: -20px;
    background-color: rgb(3, 12, 4);
    height: 60px;
    border-radius: 10px;
    overflow: visible;
    list-style-type: none;
}

.comp-img {
    position: relative;
    height: auto;
    width: 50px;
    left: 5px;
    top: 5px;
}

#sticky-note-img {
    height: 30px;
    width: auto;
    left: 15px;
    top: 15px;
}


.add-comp-btn {
    position: relative;
    background-color: rgba(255, 0, 0, 0);
    border-width: 0px;
    font-size: 25px;
    font-family: inherit;
    height: 60px;
    width: 60px;
    left: 0px;
    top: -45px;

}

#sticky-note-label {
    position: relative;
    top: -120px;
}

.comp-label {
    text-align: right;
    width: 150px;
    height: 70px;
    position: relative;
    left: -170px;
    top: -130px;
    font-size: 27px;
    visibility: hidden;
}

#sticky-note-btn:hover + #sticky-note-label  {
    visibility: visible;
}

#list-btn:hover + #list-label {
    visibility: visible ;
}

#sheet-btn:hover + #sheet-label {
    visibility: visible;
}

#color-panel-div {
    width: 135px;
    height: 227px;
    border-radius: 10px;
    display: grid;
    grid-template-columns: repeat(6, 20px);
    grid-template-rows: repeat(10, 20px);
    grid-gap: 3px;
    gap: 3px;
    padding: 10px;

    position: absolute;
    top: calc(50% - 150px);
    right: 105px;
}

.color-div{
    border: 1px solid black;
    border-radius: 3px;
}

